import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";

import Spinner from "../spinner/Spinner";
import ErrorMessage from "../errorMessage/ErrorMessage";

const withSingleInformationPage = (BaseComponent, getData) => {
  return (props) => {
    const { comicsId, characterName } = useParams();

    const [state, setState] = useState({});
    const { loading, error, getComics, getCharacterName, clearError } = getData();

    useEffect(() => {
      if (comicsId) {
        updateSingleInformationAboutComics();
      } else if (characterName) {
        updateSingleInformationAboutCharacter();
      }
    }, [comicsId, characterName]);

    const updateSingleInformationAboutComics = () => {
      clearError();
      getComics(comicsId).then(onSingleInformationLoaded);
    };

    const updateSingleInformationAboutCharacter = () => {
      clearError();
      getCharacterName(characterName).then(onSingleInformationLoaded);
    };

    const onSingleInformationLoaded = (state) => {
      setState(state);
    };

    const errorMessage = error ? <ErrorMessage /> : null;
    const spinner = loading ? <Spinner /> : null;

    return <BaseComponent state={state} errorMessage={errorMessage} spinner={spinner}></BaseComponent>;
  };
};

export default withSingleInformationPage;
