import { Link } from "react-router-dom";

import useMarvelService from "../../services/MarvelService";

import withSingleInformationPage from "./withSingleInformationPage";

import "./singleComicsPage.scss";

const SingleComicsPage = (props) => {
  const { title, description, thumbnail, pageCount, language, price } = props.state;

  const Wrapper = () => {
    return (
      <>
        <div className="single-comics">
          <img src={thumbnail} alt={title} className="single-comics__img" />
          <div className="single-comics__info">
            <h2 className="single-comics__name">{title}</h2>
            <p className="single-comics__descr">{description}</p>
            <p className="single-comics__descr">{pageCount}</p>
            <p className="single-comics__descr">Language: {language}</p>
            <div className="single-comics__price">{price}</div>
          </div>
          <Link to="/comics" className="single-comics__back">
            Back to all
          </Link>
        </div>
      </>
    );
  };

  const spinner = props.spinner;
  const errorMessage = props.errorMessage;
  const content = !(spinner || errorMessage || !props.state) ? <Wrapper /> : null;

  return (
    <>
      {content} {spinner}
      {errorMessage}
    </>
  );
};

const SingleComics = withSingleInformationPage(SingleComicsPage, useMarvelService);

export default SingleComics;
